const MOBILE_SEARCH = document.querySelector('[data-mobile-search]');

export default class Search {
  constructor () {

  }

  mobileSearch () {
    MOBILE_SEARCH.addEventListener('click', this.toggleMobileSearch);
  }

  toggleMobileSearch() {
    // No search bar is currently shown
    if (this.querySelector('i').innerHTML === 'search') {
      this.querySelector('i').innerHTML = 'cancel';
      this.classList.remove('mdl-cell--hide-tablet', 'mdl-cell--hide-desktop'); // Ensures the close button doesn't disappear if the screen is resized.

      let hide = document.querySelectorAll('.mdl-layout__drawer-button, .mdl-layout-title, .mdl-badge, .mdl-layout-spacer, .mdl-navigation');
      for (let i = 0; i < hide.length; i++) {
        hide[i].classList.add('hidden');
      }

      document.querySelector('.mdl-layout__header-row').style.paddingLeft = '16px'; // Remove margin that used to hold the menu button
      let search = document.querySelector('.mdh-expandable-search');
      search.classList.remove('mdl-cell--hide-phone');
      search.style.margin = '0 16px 0 0';
    }
    // Search bar is currently showing
    else {
      this.querySelector('i').innerHTML = 'search';
      this.classList.add('mdl-cell--hide-tablet', 'mdl-cell--hide-desktop');

      let show = document.querySelectorAll('.mdl-layout__drawer-button, .mdl-layout-title, .mdl-badge, .mdl-layout-spacer, .mdl-navigation');
      for (let i = 0; i < show.length; i++) {
        show[i].classList.remove('hidden');
      }

      document.querySelector('.mdl-layout__header-row').style.paddingLeft = '';
      let search = document.querySelector('.mdh-expandable-search');
      search.classList.add('mdl-cell--hide-phone');
      search.style.margin = '0 50px';
    }
  }
}
